/**
 * Elegant Jupyter Widget Implementation
 * 
 * Pure, minimal wrapper around our core visualization engine.
 * Philosophy: Thin interface, maximum elegance, zero duplication.
 */

import {
    DOMWidgetModel,
    DOMWidgetView,
    ISerializers,
} from '@jupyter-widgets/base';

import { JupyterGroggyView } from './core';

export const MODULE_NAME = 'groggy-widgets';
export const MODULE_VERSION = '0.1.0';

/**
 * Elegant Graph Model - Pure data synchronization
 */
export class GroggyGraphModel extends DOMWidgetModel {
    defaults() {
        return {
            ...super.defaults(),
            _model_name: GroggyGraphModel.model_name,
            _model_module: GroggyGraphModel.model_module,
            _model_module_version: GroggyGraphModel.model_module_version,
            _view_name: GroggyGraphModel.view_name,
            _view_module: GroggyGraphModel.view_module,
            _view_module_version: GroggyGraphModel.view_module_version,
            
            // Graph data (elegant sync with Python)
            nodes: [],
            edges: [],
            node_positions: {},
            
            // Visualization state (elegant configuration)
            layout_algorithm: 'force-directed',
            theme: 'light',
            width: 800,
            height: 600,
            title: 'Graph Visualization',
            
            // Interactive state (elegant real-time sync)
            selected_nodes: [],
            hovered_node: '',
            camera_position: { x: 0, y: 0, zoom: 1.0 },
            is_dragging: false,
            
            // Feature configuration (elegant control)
            enable_drag: true,
            enable_pan: true,
            enable_zoom: true,
            enable_animations: true,
            animation_duration: 300,
            
            // Style configuration (elegant theming)
            style_config: {}
        };
    }
    
    static serializers: ISerializers = {
        ...DOMWidgetModel.serializers,
        // Custom serializers for complex data types if needed
    };
    
    static model_name = 'GroggyGraphModel';
    static model_module = MODULE_NAME;
    static model_module_version = MODULE_VERSION;
    static view_name = 'GroggyGraphView';
    static view_module = MODULE_NAME;
    static view_module_version = MODULE_VERSION;
}

/**
 * Elegant Graph View - Pure visual interface
 */
export class GroggyGraphView extends DOMWidgetView {
    private groggyView: JupyterGroggyView | null = null;
    
    /**
     * Elegant rendering - minimal setup, maximum power
     */
    render(): void {
        // Create elegant container
        this.el.classList.add('groggy-widget-container');
        this.el.style.cssText = `
            border: 1px solid #ddd;
            border-radius: 4px;
            background: #fafafa;
            position: relative;
            overflow: hidden;
        `;
        
        // Initialize our elegant core bridge
        this.groggyView = new JupyterGroggyView(this.el, this.model);
        
        // Set up elegant message handling
        this.model.on('msg:custom', this.handleCustomMessage, this);
        
        // Elegant initialization
        this.groggyView.initialize();
        
        console.log('âœ¨ Elegant Groggy widget rendered');
    }
    
    /**
     * Elegant custom message handling
     */
    private handleCustomMessage(content: any): void {
        if (this.groggyView) {
            this.groggyView.handleCustomMessage(content);
        }
    }
    
    /**
     * Elegant cleanup
     */
    remove(): void {
        if (this.groggyView) {
            this.groggyView.destroy();
            this.groggyView = null;
        }
        
        super.remove();
    }
    
    /**
     * Elegant resize handling
     */
    onResize(): void {
        // Automatic resize handling through model sync - elegant!
        if (this.groggyView) {
            const width = this.el.clientWidth;
            const height = this.el.clientHeight;
            
            if (width > 0 && height > 0) {
                this.model.set('width', width);
                this.model.set('height', height);
                this.model.save_changes();
            }
        }
    }
}